import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { Container ,Paper, Button} from '@material-ui/core';

const realDir = "https://andr7st-eng.herokuapp.com/student/"



const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1)
    },
  },
}));

export default function Student() {
  const fondoW = { background: '#FFF' }
  const classes = useStyles()
  const paperStyle = { background: '#DCC3F4', padding: '50px 20px', width: 600, margin:"20px auto"}
  const [name, setName] = useState('')
  const[students,setStudents]=useState([])
  const [address,setAddress] = useState('')
  const actualizar = ()=> {fetch(`${realDir}getAll`).then(res => res.json()).then((result) => {setStudents(result)})}

    const handleClick=(e)=>{

    e.preventDefault()
    const student={name,address}
    console.log(student)
    fetch(`${realDir}add`,{
      method:"POST",
      headers:{"Content-Type":"application/json"},
      body:JSON.stringify(student)
      }).then(()=>{
        console.log("New Student added")
        setName('')
        setAddress('')
        actualizar()
    })}

    useEffect(()=> {fetch(`${realDir}getAll`).then(res => res.json()).then((result) => {  setStudents(result)})},[])



  return (

    <Container>
      <Paper elevation={3} style={paperStyle}>
        <h1 style={{color:'blue'}}> <u>Add Student</u> </h1>
        <form className={classes.root} noValidate autocomplete="off"> 
          <TextField id="outline-basic" label="Nombre Estudiante" variant="outlined" 
          value={name} 
          onChange={(e)=>setName(e.target.value)} fullWidth style={fondoW}/>
          <TextField id="outline-basic" label="Direccion" variant="outlined" 
          value={address}
          onChange={(e)=>setAddress(e.target.value)} fullWidth style={fondoW} />
          <Button variant="contained" color="success" onClick={handleClick}>
            Aade
          </Button>
        </form>
      </Paper>
      {/**
       * 
      */}
      <h1>Students</h1>
      <Paper elevation={3} style={paperStyle}>

  
      {students.map(student=>(
        <Paper elevation={6} style={{margin:"10px",padding:"15px", textAlign:"left"}} key={student.id}>
          Id:{student.id}<br/>
          Name:{student.name}<br/>
          Address:{student.address}
        </Paper>
      ))}
      </Paper>
      <Paper elevation={3} style={paperStyle}>
        Ejercicio realizado por Andrs Segura
      </Paper>
    </Container>
  )
}